import React, {Component} from 'react';
import 'semantic-ui-css/semantic.min.css';
import {MarketOrders} from "./marketorders";
import {MyOrders} from "./myorders";
import BasePage from "../basepage";

export class CustomerPag extends BasePage {
    constructor(props) {
        super(props, {
            orderType: 0,
        });
    }

    render() {
        return (
            <div>
                <div className="ui top attached tabular menu">
                    <a className={this.state.orderType == 1 ? "active item" : "item"} onClick={() => {
                        this.setState({orderType: 1})
                    }}>买入</a>
                    <a className={this.state.orderType == 0 ? "active item" : "item"} onClick={() => {
                        this.setState({orderType: 0})
                    }}>卖出</a>
                    <a className={this.state.orderType == 2 ? "active item" : "item"} onClick={() => {
                        this.setState({orderType: 2})
                    }}>订单</a>
                </div>
                <div className="ui bottom attached segment">
                    {
                        this.state.orderType == 2 ? <MyOrders pk={this.state.pk}/> :
                            <MarketOrders pk={this.state.pk} orderType={this.state.orderType}/>
                    }
                </div>
            </div>
            // <StickyContainer>
            //     <Sticky>
            //         {({style, isSticky, wasSticky, distanceFromTop, distanceFromBottom, calculatedHeights}) => (
            //             <div style={{
            //                 ...style,
            //                 zIndex: 3,
            //                 color: 'white',
            //                 backgroundColor: '#fff'
            //             }}>
            //
            //             </div>
            //         )}
            //     </Sticky>
            //
            // </StickyContainer>
        )
    }
}